import React from 'react';
import './Header.css';
import SectionHead from './SectionHead';
import OfferElems from './Offer/OfferElems';
import articleContent from './articleContent';

function Offer() {

  const article = articleContent.find(article => article.name === 'oferta');

    return (
        <section className="container" id="offer">
          <SectionHead header="oferta" />

          <div id="offer_text">
            <p>{article.content}</p>
          </div>

          <OfferElems />
        </section>
    )
}

export default Offer;