import React from 'react';
import '../Header.css';
import '../index.css';
import {
    Link
  } from "react-router-dom";

function OfferElems() {

    return (
        <div className="row text-uppercase">
            <div className="col-6 col-md-4 col-lg-3">
              <Link to="/muzg-garage-app/oferta/diagnostyka">
              <div className="card" style={{flex:1}} id="diag">
                <img className="card-img" src="/muzg-garage-app/img/diag.jpg" alt="Diagnostyka" />
                <img className="card-img img_hover" src="/muzg-garage-app/img/diag_h.jpg" alt="Diagnostyka on mouse hover" />
                <div className="card-img-overlay text-center d-flex">
                  <h4 className=" text-light card-title align-self-center mx-auto font-weight-bold">Diagnostyka komputerowa</h4>
                </div>
              </div>
              </Link>
            </div>

            <div className="col-6 col-md-4 col-lg-3">
              <Link to="/muzg-garage-app/oferta/naprawy-mechaniczne">
              <div className="card" style={{flex:1}}>
                <img className="card-img" src="/muzg-garage-app/img/mechanika.jpg" alt="Naprawy mechaniczne" />
                <img className="card-img img_hover" src="/muzg-garage-app/img/mechanika_h.jpg" alt="Naprawy mechaniczne hover" />
                  <div className="card-img-overlay text-center d-flex">
                    <h4 className=" text-light card-title align-self-center mx-auto font-weight-bold">Naprawy mechaniczne</h4>
                  </div>
              </div>
              </Link>
            </div>

            <div className="col-6 col-md-4 col-lg-3">
              <Link to="/muzg-garage-app/oferta/chip-tuning">
              <div className="card" style={{flex:1}}>
                <img className="card-img" src="/muzg-garage-app/img/chip.jpg" alt="Chip tuning" />
                <img className="card-img img_hover" src="/muzg-garage-app/img/chip_h.jpg" alt="Chip tuning hover" />
                <div className="card-img-overlay text-center d-flex">
                  <h4 className=" text-light card-title align-self-center mx-auto font-weight-bold">Chip tuning</h4>
                </div>
              </div>
              </Link>
            </div>

            <div className="col-6 col-md-4 col-lg-3">
              <Link to="/muzg-garage-app/oferta/klimatyzacja">
              <div className="card" style={{flex:1}}>
                <img className="card-img-top" src="/muzg-garage-app/img/ac.jpg" alt="Klimatyzacja" />
                <img className="card-img img_hover" src="/muzg-garage-app/img/ac_h.jpg" alt="Klimatyzacja hover" />
                <div className="card-img-overlay text-center d-flex">
                  <h4 className=" text-light card-title align-self-center mx-auto font-weight-bold">Serwis klimatyzacji</h4>
                </div>
              </div>
            </Link>
            </div>
            
            <div className="col-6 col-md-4 col-lg-3">
              <Link to="/muzg-garage-app/oferta/tuning-silnika">
              <div className="card" style={{flex:1}}>
                <img className="card-img" src="/muzg-garage-app/img/engine.jpg" alt="Tuning" />
                <img className="card-img img_hover" src="/muzg-garage-app/img/engine_h.jpg" alt="Tuning hover" />
                <div className="card-img-overlay text-center d-flex">
                  <h4 className=" text-light card-title align-self-center mx-auto font-weight-bold">Tuning silnika</h4>
                </div>
              </div>
            </Link>
            </div>

            <div className="col-6 col-md-4 col-lg-3">
              <Link to="/muzg-garage-app/oferta/przeglad">
              <div className="card" style={{flex:1}}>
                <img className="card-img" src="/muzg-garage-app/img/zakup.jpg" alt="Przegląd" />
                <img className="card-img img_hover" src="/muzg-garage-app/img/zakup_h.jpg" alt="Przegląd hover" />
                <div className="card-img-overlay text-center d-flex">
                  <h4 className=" text-light card-title align-self-center mx-auto font-weight-bold">Przegląd przedzakupowy</h4>
                </div>
              </div>
              </Link>
            </div>
            
            <div className="col-6 col-md-4 col-lg-3">
              <Link to="/muzg-garage-app/oferta/wulkanizacja">
              <div className="card" style={{flex:1}}>
                <img className="card-img-top" src="/muzg-garage-app/img/tire.jpg" alt="Wulkanizacja" />
                <img className="card-img img_hover" src="/muzg-garage-app/img/tire_h.jpg" alt="Wulkanizacja hover" />
                <div className="card-img-overlay text-center d-flex">
                  <h4 className=" text-light card-title align-self-center mx-auto font-weight-bold">Wulkanizacja</h4>
                </div>
              </div>
              </Link>
            </div>
            <div className="col-6 col-md-4 col-lg-3">
              <Link to="/muzg-garage-app/oferta/elektromechanika">
              <div className="card" style={{flex:1}}>
                <img className="card-img-top" src="/muzg-garage-app/img/electro.jpg" alt="Elektromechanika" />
                <img className="card-img img_hover" src="/muzg-garage-app/img/electro_h.jpg" alt="Elektromechanika hover" />
                <div className="card-img-overlay text-center d-flex">
                  <h4 className=" text-light card-title align-self-center mx-auto font-weight-bold">Elektro-mechanika</h4>
                </div>
              </div>
              </Link>
            </div>
          </div>

    )
}
export default OfferElems;