import React from 'react';
import './Header.css';

import Menu from './Menu';

function Header() {
  window.onscroll = () => {
    if (document.body.scrollTop > 50 || document.documentElement.scrollTop > 50) {
        document.getElementById("contact-row").style.height = "0px";
        document.getElementById("contact-row").style.opacity = "0.0";
        document.getElementById("contact-row").style.border = "none";
    } else {
      document.getElementById("contact-row").style.height = "45px";
      document.getElementById("contact-row").style.opacity = "1.0";
      document.getElementById("contact-row").style.borderBottom = "1px solid rgb(80, 80, 80)";
    }
}
  return (
    <header className="App">
      <div className="fixed-top" id="head">
        <div className="d-none d-md-flex text-light align-middle" id="contact-row">
          <div id="phone-home" className="pl-5 pr-3 my-2">
          +48 601 694 997
          </div>

          <div id="facelink" className="pl-3 my-2">
            <a href="https://www.facebook.com/muzggarage">
              <img width="30" src="/muzg-garage-app/img/fb_logo.png" alt="logo_facebook" />
            </a>
          </div>
        </div>
        <Menu />
      </div>
    </header>
  )}

export default Header;
