import React from 'react';
import './Header.css';
import './index.css';
import SectionHead from './SectionHead';
import OfferElems from './Offer/OfferElems';
import SubTop from './SubTop';

import articleContent from './articleContent';

function Oferta({match}){

    const name = match.params.name;
    const offer = articleContent.find(offer => offer.name === name);

    return(
        <div>
        <SubTop text={offer.title} />
        <section className="container">
        
          <div className="row">
            <div className="col-12">
            {offer.content.map((content, key) => (
                  <p key={key}>{content}</p>
              ))}
            </div>
          </div>
        <SectionHead header="pozostała oferta" />
        <OfferElems />
      </section>
      </div>
    )
}

export default Oferta;