import React from 'react';
import './Header.css';
import {
    Link
  } from "react-router-dom";
  import { NavHashLink } from 'react-router-hash-link';

function Menu() {

    return (
        <nav className="navbar navbar-dark navbar-expand-md opacity">
            <Link className="menuTop d-inline-block navbar-brand text-center mx-auto" to="/muzg-garage-app">
              <img src="/muzg-garage-app/img/muzg_logo_trans.png" width="100" alt="logo_muzg_garage" />
            </Link>

            <button className="navbar-toggler btn" type="button" data-toggle="collapse" data-target="#mainMenu" 
              aria-controls="mainMenu" aria-expanded="false" aria-haspopup="true">
              <span className="navbar-toggler-icon"></span>
            </button>

            <div className="collapse navbar-collapse ml-5" id="mainMenu">
              <ul className="navbar-nav font-weight-normal">
                <li className="nav-item dropdown" id="oferta">
                  <a className="nav-link dropdown-toggle menu-offer-button"  data-toggle="dropdown" role="button" id="submenu" aria-expanded="false">OFERTA</a>

                  <div className="dropdown-menu dropdown-menu-left w-auto shadow p-0" aria-labelledby="submenu">
                    <Link to="/muzg-garage-app/oferta/diagnostyka" data-toggle="collapse" data-target=".navbar-collapse.show" className="menuTop dropdown-item">Diagnostyka komputerowa</Link>
                    <Link to="/muzg-garage-app/oferta/naprawy-mechaniczne" data-toggle="collapse" data-target=".navbar-collapse.show" className="menuTop dropdown-item">Naprawy mechaniczne</Link>
                    <Link to="/muzg-garage-app/oferta/wulkanizacja" data-toggle="collapse" data-target=".navbar-collapse.show" className="menuTop dropdown-item">Wulkanizacja</Link>
                    <Link to="/muzg-garage-app/oferta/klimatyzacja" data-toggle="collapse" data-target=".navbar-collapse.show" className="menuTop dropdown-item">Serwis klimatyzacji</Link>
                    <Link to="/muzg-garage-app/oferta/elektromechanika" data-toggle="collapse" data-target=".navbar-collapse.show" className="menuTop dropdown-item">Elektromechanika</Link>
                    <Link to="/muzg-garage-app/oferta/chip-tuning" data-toggle="collapse" data-target=".navbar-collapse.show" className="menuTop dropdown-item">Chip Tuning</Link>
                    <Link to="/muzg-garage-app/oferta/tuning-silnika" data-toggle="collapse" data-target=".navbar-collapse.show" className="menuTop dropdown-item">Tuning silnika</Link>
                    <Link to="/muzg-garage-app/oferta/przeglad" data-toggle="collapse" data-target=".navbar-collapse.show" className="menuTop dropdown-item">Przegląd przedzakupowy</Link>
                  </div>

                </li>
  
                <li className="nav-item">
                  <NavHashLink data-toggle="collapse" data-target=".navbar-collapse.show" className="nav-link" to="/muzg-garage-app/#about" >O FIRMIE</NavHashLink>
                </li>
                
                <li className="nav-item">
                  <NavHashLink data-toggle="collapse" data-target=".navbar-collapse.show" className="nav-link" to="/muzg-garage-app/#contact">KONTAKT</NavHashLink>
                </li>
                
              </ul> 
            </div>
        </nav>
    )
}

export default Menu;