import React from 'react';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';
import './Header.css';
import './index.css';
import './Header_Carousel.css';

function CarouselH(){

    return (
        <Carousel fade pause={false} indicators={false} id="carousel">
            <Carousel.Item interval={6000}>
                <Carousel.Caption className="text-uppercase carousel_cap">
                    <h1 class="font-weight-bold display-md-4 d-md-4">Napędza nas pasja do motoryzacji</h1>
                    <p>Umów się na wizytę ze specjalistą</p>
                    <Button variant="dark" href="#contact" className="mt-md-3 carousel_btn">KONTAKT</Button>
                </Carousel.Caption>
                <img className="d-block w-100" src="/muzg-garage-app/img/slider_1.jpg" alt="First slide" />
            </Carousel.Item>
            <Carousel.Item interval={6000}>
                <Carousel.Caption className="text-uppercase carousel_cap">
                <h1 class="font-weight-bold display-md-4 d-md-4">Szybka diagnostyka każdej usterki</h1>
                <p>Umów się na wizytę ze specjalistą</p>
                <Button variant="dark" href="#contact" className="mt-md-3 carousel_btn">KONTAKT</Button>
                </Carousel.Caption>
                <img class="d-block w-100" src="/muzg-garage-app/img/slider_2.jpg" alt="Second slide" />
            </Carousel.Item>
            <Carousel.Item interval={6000}>
                <Carousel.Caption className="text-uppercase carousel_cap">
                <h1 class="font-weight-bold display-md-4 d-md-4">Kompleksowa obsługa samochodu</h1>
                <p>Sprawdź naszą ofertę</p>
                <Button variant="dark" href="#offer" className="mt-md-3 carousel_btn">OFERTA</Button>
                </Carousel.Caption>
                <img class="d-block w-100" src="/muzg-garage-app/img/slider_3.jpg" alt="Third slide" />
            </Carousel.Item>
            
        </Carousel >     
    
)}

export default CarouselH;