import React from 'react';
import SectionHead from './SectionHead';
import './Header.css';

function Contact() {
    return (
        <section className="container" id="contact">
          <SectionHead header="kontakt" />
          <div className="row text-center ">
            <div className="col-md-4">
              <img src="/muzg-garage-app/img/phone.png" alt="phone_icon" />
              <p className="text-uppercase font-weight-bold mb-md-3 contact_p">Zadzwoń</p>
              <p>+48 601 694 997</p>
            </div>

            <div className="col-md-4">
              <img src="/muzg-garage-app/img/email.png" alt="email_icon" />
              <p className="text-uppercase font-weight-bold mb-md-3 contact_p">Napisz</p>
              <p>biuro@muzggarage.pl</p>
            </div>

            <div className="col-md-4">
              <img src="/muzg-garage-app/img/location.png" alt="location_icon" />
              <p className="text-uppercase font-weight-bold mb-md-3 contact_p">Przyjedź</p>
              <p>
                MUZG GARAGE <br />Stradomka 90<br /> 32-742 Sobolów
              </p>
            </div>
            </div>
          

          <div className="row">
            <div className="col-10 mx-auto">
              <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d20551.460104362486!2d20.299403028904997!3d49.918841531940544!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x47163db498d7bd8f%3A0x5034759fbcfb7677!2sMuzg%20Garage!5e0!3m2!1spl!2spl!4v1624195091719!5m2!1spl!2spl" title="MapaMuzg" width="100%" height="450" style={{border:0}} allowFullScreen="" loading="lazy"></iframe>
            </div>
          </div>
        </section>
    )}


export default Contact;