import React from 'react';
import SectionHead from './SectionHead';
import articleContent from './articleContent';

function About(){
  const article = articleContent.find(article => article.name === 'o-firmie');
    return(
        <section id="about">
        <div className="container">
          <SectionHead header="O Firmie" />
          <div className="row">
            <div className="col-12">
              <p>{article.content}</p>
            </div>
          </div>
        </div>
      </section>
    )
}

export default About;