
import React from 'react';
import './Header.css';
import './index.css';
import './Header_Carousel.css';
function SubTop(props){
    
    return (
        <div id="subTop" className="page mb-5" >
            <img src="/muzg-garage-app/img/sub_page.jpg" className="sub_img" alt="header_sub" />
            <div className="sub_text w-100 mt-4 text-center">
                <h1 className="text-uppercase text-light">{props.text}</h1>
            </div>
        </div>
    )
}

export default SubTop;