import React from 'react';

import './index.css';
import './Header.css';
import Header from './Header';
import Contact from './Contact';
import Offer from './Offer';
import About from './About';
import CarouselH from './CarouselH';
import Footer from './Footer';
import ScrollToTop from './ScrollToTop';
import Oferta from './Oferta';
import NotFound404 from './NotFound404';

import {
    BrowserRouter as Router,
    Switch,
    Route,
  } from "react-router-dom";

import {
    CSSTransition,
    TransitionGroup
} from "react-transition-group"

function App() {

    return (
    <div className="App">
        <Router>
        <ScrollToTop />
            <Header />
            
            
            <Route render={({location}) => (
            <TransitionGroup>
            <CSSTransition
                key = {location.key}
                timeout={300}
                classNames="fade">

            <Switch location={location}>

                <Route path="/muzg-garage-app/oferta/:name" component={Oferta} />
                
                <Route exact path="/muzg-garage-app">
                    <CarouselH />
                    <Route exact path="/muzg-garage-app/oferta" component={Offer} />
                    <Offer />
                    <About path="/muzg-garage-app/o-firmie" />
                    <Contact path="/muzg-garage-app/kontakt"/>
                </Route>
                
                
            </Switch>
            </CSSTransition>
            </TransitionGroup>
            )} />
            <Footer />
        </Router> 
        
    </div>
    )
}

export default App;